<script>
import { ref, reactive, onMounted, watch } from "vue"
import { useRouter } from "vue-router"
import { Toast } from "../../components/global/swal"
import Header from "../../components/global/header/HeaderArea2.vue"
import DiseaseType from "../../components/global/DiseaseType.vue"
import {
  apiGetResumeList,
  apiPostResumeInfo,
  apiGetJobType,
  apiGetWorkHourType,
  apiGetExperience,
  apiGetCityArea,
  apiUpload,
} from "@/api/api"
import { useJobStore, useCityStore, useExperienceStore } from "@/store/global"
import { focusInput } from "@/utils/fixFooter"
// import { ExtCall } from "@/utils/extCall"

export default {
  setup() {
    const jobStore = useJobStore()
    const cityStore = useCityStore()
    const experienceStore = useExperienceStore()
    const router = useRouter()
    const data = reactive({})
    // 控制頁面
    let stepStyle = ref(["nav-link active", "nav-link", "nav-link", "nav-link"])
    let showPage = ref("page1")
    let title = ref("基本資料")
    const form1 = ref(null)
    const save = (e) => {
      e.preventDefault()
      if (form1.value.reportValidity()) {
        if (data.servicePlace.length == 0) {
          Toast("請至少選擇一個工作地點")
        } else {
          showPage.value = "page2"
          stepStyle.value = [
            "nav-link visited",
            "nav-link active",
            "nav-link",
            "nav-link",
          ]
          title.value = "工作地點"
        }
      }
    }
    const save2 = () => {
      if (data.selectArea.length == 0 && data.selectAllCityId.length == 0) {
        Toast("請至少選擇一個工作地點")
      } else {
        showPage.value = "page3"
        stepStyle.value = [
          "nav-link visited",
          "nav-link visited",
          "nav-link active",
          "nav-link",
        ]
        title.value = "照護經驗(複選)"
      }
    }
    const save3 = () => {
      if (data.diseaseId.length == 0) {
        Toast("請至少選擇一個照護經驗")
      } else {
        showPage.value = "page4"
        stepStyle.value = [
          "nav-link visited",
          "nav-link visited",
          "nav-link visited",
          "nav-link active",
        ]
        title.value = "相關證照"
      }
    }
    const save4 = async () => {
      // console.log(data)
      try {
        let msg = await apiPostResumeInfo(data)
        if (msg.data == "新增成功") {
          Toast(msg.data)
          router.push({ path: "/resume/list" })
        } else {
          Toast(msg.message)
        }
      } catch (error) {
        console.log(error)
      }
    }
    const backToPage1 = () => {
      showPage.value = "page1"
      stepStyle.value = ["nav-link active ", "nav-link", "nav-link", "nav-link"]
      title.value = "基本資料"
    }
    const backToPage2 = () => {
      showPage.value = "page2"
      stepStyle.value = [
        "nav-link visited ",
        "nav-link active",
        "nav-link",
        "nav-link",
      ]
      title.value = "工作地點"
    }
    const backToPage3 = () => {
      showPage.value = "page3"
      stepStyle.value = [
        "nav-link visited",
        "nav-link visited",
        "nav-link visited",
        "nav-link active",
      ]
      title.value = "照護經驗"
    }
    //page1預設值
    data.jobTypeId = "" //v-model
    // data.workPeriodId0 = 0
    data.workPeriodId = []
    data.otherJobTime = 0 //沒填要回0
    data.seniorityId = 1
    data.servicePlace = []
    data.startDate = ""
    data.wage = {
      home: null,
      hospital: null,
      fullDay: {
        home: null,
        hospital: null,
      },
    }

    let allIdentity = ref() // 全部類別
    let identity = ref([]) // 已經有的類別
    let workHourType = ref([
      {
        id: 5,
        SlotType: "F",
        name: "8小時",
      },
      {
        id: 4,
        SlotType: "F",
        name: "10小時",
      },
      {
        id: 2,
        SlotType: "F",
        name: "12小時",
      },
      {
        id: 1,
        SlotType: "F",
        name: "24小時",
      },
    ]) //工作時段選項
    let experienceType = ref([])
    //page2 預設值
    let cityArea = ref([])
    data.selectArea = []
    data.selectAllCityId = []
    const checkAll = (val) => {
      let areaList = []
      if (data.selectAllCityId.includes(val.id)) {
        // console.log('全選')
        val.areas.forEach((x) => data.selectArea.push(x.id))
      } else {
        // console.log('取消全選')
        val.areas.forEach((x) => areaList.push(x.id))
        // console.log(111, areaList)
        data.selectArea = data.selectArea.filter((e) => {
          return areaList.indexOf(e) === -1
        })
      }
    }
    const cancelAll = (item, item2, $event) => {
      if ($event.target.checked == false) {
        // console.log("取消全選")
        data.selectAllCityId = data.selectAllCityId.filter((x) => x != item.id)
      }
      // console.log("data.selectAllCityId",data.selectAllCityId)
    }
    //page3 預設值
    data.diseaseId = []
    //取病症代碼
    const updateDiseaseData = (val) => {
      data.diseaseId = val
      // console.log(data.diseaseId)
    }
    //page4 預設值
    data.license = {
      certificateCode: "",
      healthFormPhotoId: "",
      mainLicensePhotoId: "",
      otherLicensePhotoId: "",
      policeCriminalRecord: "",
    }
    //檔案上傳
    const myUploadFile = ref(null)
    const myUploadFile2 = ref(null)
    const myUploadFile3 = ref(null)
    const myUploadFile4 = ref(null)

    const useIdCheckFileSize = async (id, requestData) => {
      //檢查上傳檔案大小
      const fileInput = document.getElementById(`formFile${id}`)
      console.log(fileInput)
      const getFile = fileInput?.files?.[0]
      if (!getFile) return false

      const fileSize = getFile?.size / 1024 / 1024
      if (fileSize > process.env.VUE_APP_UPLOAD_LIMIT) {
        fileInput.value = ""
        Toast(`圖片檔案大小上限為${process.env.VUE_APP_UPLOAD_LIMIT}MB`)
        return false
      }
      const response = await apiUpload(requestData)
      if (response.status) {
        // 成功回拋 response
        return response
      } else {
        fileInput.value = ""
        const errorMessage = response.message || "上傳失敗"
        Toast(errorMessage)
        return false
      }
    }

    const handleFileUpload = async (licenseId, UploadFile) => {
    //   try {
        let requestData = new FormData()
        requestData.append("singleFile", UploadFile.files[0])
        requestData.append("uploadType", "ResumeArea")
        requestData.append("jobType", data.jobTypeId)

        if (licenseId == 1) {
          const response = await useIdCheckFileSize(1, requestData)
          if (!response) return
          data.license.mainLicensePhotoId = response.data.id
        } else if (licenseId == 2) {
          data.license.healthFormPhotoId = response.data.id
          const response = await useIdCheckFileSize(2, requestData)
          if (!response) return
        } else if (licenseId == 3) {
          data.license.policeCriminalRecord = response.data.id
          const response = await useIdCheckFileSize(3, requestData)
          if (!response) return
        } else {
          data.license.otherLicensePhotoId = response.data.id
          const response = await useIdCheckFileSize(4, requestData)
          if (!response) return
        }
    //   } catch (error) {
    //     console.log("error", error)
    //   }
    }

    onMounted(async () => {
      focusInput()
      try {
        //取類別選項
        if (jobStore.jobTypeList == undefined) {
          // call api
          allIdentity.value = await apiGetJobType()
          allIdentity.value = allIdentity.value.data.data
          jobStore.jobTypeList = allIdentity.value
        } else {
          allIdentity.value = jobStore.jobTypeList
        }
        let getResumeList = await apiGetResumeList()
        getResumeList.data.forEach((item) => identity.value.push(item.title))
        allIdentity.value = allIdentity.value.filter(
          (item) => !identity.value.includes(item.name)
        )
        //服務年資列表
        if (experienceStore.experienceTypeList == undefined) {
          // call api
          experienceType.value = await apiGetExperience()
          experienceType.value = experienceType.value.data.data
          experienceStore.experienceTypeList = experienceType.value
        } else {
          experienceType.value = experienceStore.experienceTypeList
        }
        //取縣市地區
        if (cityStore.cityAreaList == undefined) {
          // console.log('沒有')
          // call api
          cityArea.value = await apiGetCityArea()
          cityArea.value = cityArea.value.data
          cityStore.cityAreaList = cityArea.value
        } else {
          // console.log('有')
          cityArea.value = cityStore.cityAreaList
        }
      } catch (error) {
        console.log(error)
      }
    })
    watch(
      () => data.jobTypeId,
      async () => {
        //取工作時段
        workHourType.value = await apiGetWorkHourType({
          jobTypeId: data.jobTypeId,
        })
        workHourType.value = workHourType.value.data.data
        // data.workPeriodId = workHourType.value[0].id
        // console.log("workHourType", workHourType.value)
      }
    )

    let isShowTimeInput = ref(0)
    const showTimeInput = () => {
      if (isShowTimeInput.value == 0) {
        isShowTimeInput.value = 1
      } else {
        isShowTimeInput.value = 0
      }
    }

    return {
      isShowTimeInput,
      showTimeInput,
      data,
      stepStyle,
      showPage,
      title,
      save,
      save2,
      save3,
      save4,
      backToPage1,
      backToPage2,
      backToPage3,
      allIdentity,
      workHourType,
      experienceType,
      form1,
      cityArea,
      updateDiseaseData,
      checkAll,
      cancelAll,
      handleFileUpload,
      myUploadFile,
      myUploadFile2,
      myUploadFile3,
      myUploadFile4,
    }
  },
  components: {
    Header,
    DiseaseType,
  },
}
</script>

<template>
  <Header v-if="showPage == 'page1'" path="/resume/list" />
  <Header v-if="showPage == 'page2'" :clickFun="backToPage1" />
  <Header v-if="showPage == 'page3'" :clickFun="backToPage2" />
  <Header v-if="showPage == 'page4'" :clickFun="backToPage3" />

  <div class="resumeAdd">
    <div class="mb-3 step-container">
      <ul class="nav justify-content-center">
        <!-- 已完成 visited，目前 active -->
        <li class="nav-item">
          <a :class="stepStyle[0]"><span>1</span></a>
        </li>
        <li class="nav-item">
          <a :class="stepStyle[1]"><span>2</span></a>
        </li>
        <li class="nav-item">
          <a :class="stepStyle[2]"><span>3</span></a>
        </li>
        <li class="nav-item">
          <a :class="stepStyle[3]"><span>4</span></a>
        </li>
      </ul>
    </div>
    <div class="title">
      <p>{{ title }}</p>
    </div>
    <hr />
    <div class="page1" v-show="showPage == 'page1'">
      <form ref="form1">
        <div class="mb-3">
          <label class="form-label">履歷身分<span class="star">*</span></label>
          <div class="main" v-show="showPage == 'page1'">
            <div class="input-group">
              <select
                class="col form-control form-select"
                v-model="data.jobTypeId"
                required
              >
                <option disabled value="">請選擇履歷身分</option>
                <option
                  v-for="item in allIdentity"
                  :value="item.id"
                  :key="item.id"
                >
                  {{ item.name }}
                </option>
              </select>
            </div>
          </div>
        </div>
        <div class="mb-3">
          <label class="form-label">工作時段<span class="star">*</span></label>
          <div class="input-list">
            <div
              class="form-check-inline"
              v-for="item in workHourType"
              :key="item.id"
            >
              <input
                type="checkbox"
                class="form-check-input"
                :value="item.id"
                :id="'work' + item.id"
                v-model="data.workPeriodId"
              />
              <label class="form-check-label" :for="'work' + item.id">{{
                item.name
              }}</label>
            </div>
            <div class="form-check-inline">
              <input
                type="checkbox"
                class="form-check-input"
                value="work99"
                id="work99"
                @change="showTimeInput"
              />
              <label class="form-check-label" for="work99">其他</label>
            </div>
            <input
              type="number"
              class="form-control mt-1"
              placeholder="請輸入其他工作時間"
              v-model="data.otherJobTime"
              min="1"
              max="24"
              v-if="isShowTimeInput"
            />
          </div>
        </div>
        <div class="mb-3">
          <label class="form-label">服務年資<span class="star">*</span></label>
          <div class="input-list">
            <div
              class="form-check-inline"
              v-for="item in experienceType"
              :key="item.id"
            >
              <input
                type="radio"
                class="form-check-input"
                :value="item.id"
                :id="'exp' + item.id"
                v-model="data.seniorityId"
              />
              <label class="form-check-label" :for="'exp' + item.id">{{
                item.name
              }}</label>
            </div>
          </div>
        </div>
        <div class="mb-3">
          <label class="form-label">工作地點<span class="star">*</span></label>
          <div class="input-list">
            <div class="form-check-inline">
              <input
                type="checkbox"
                class="form-check-input"
                value="1"
                id="live1"
                v-model="data.servicePlace"
              />
              <label class="form-check-label" for="live1">居家</label>
            </div>
            <div class="form-check-inline">
              <input
                type="checkbox"
                class="form-check-input"
                value="2"
                id="live2"
                v-model="data.servicePlace"
              />
              <label class="form-check-label" for="live2">醫院</label>
            </div>
            <div class="form-check-inline">
              <input
                type="checkbox"
                class="form-check-input"
                value="3"
                id="live3"
                v-model="data.servicePlace"
              />
              <label class="form-check-label" for="live3">療養院</label>
            </div>
          </div>
        </div>
        <div class="mb-3">
          <label class="form-label"
            >可開始工作日期<span class="star">*</span></label
          >
          <input
            type="date"
            class="form-control"
            v-model="data.startDate"
            required
          />
        </div>
        <div class="mb-3">
          <label class="form-label"
            >收費標準(居家/小時)<span class="star">*</span></label
          >
          <input
            type="number"
            class="form-control"
            placeholder="請輸入時薪"
            v-model="data.wage.home"
            min="100"
            required
          />
        </div>
        <div class="mb-3">
          <label class="form-label"
            >收費標準(醫院/小時)<span class="star">*</span></label
          >
          <input
            type="number"
            class="form-control"
            placeholder="請輸入時薪"
            v-model="data.wage.hospital"
            min="100"
            required
          />
        </div>
        <div class="mb-3">
          <label class="form-label"
            >收費標準(居家/日)<span class="star">*</span></label
          >
          <input
            type="number"
            class="form-control"
            placeholder="請輸入時薪"
            v-model="data.wage.fullDay.home"
            min="100"
            required
          />
        </div>
        <div class="mb-3">
          <label class="form-label"
            >收費標準(醫院/日)<span class="star">*</span></label
          >
          <input
            type="number"
            class="form-control"
            placeholder="請輸入時薪"
            v-model="data.wage.fullDay.hospital"
            min="100"
            required
          />
        </div>
      </form>
    </div>
    <div class="page1" v-show="showPage == 'page2'">
      <div class="mb-3">
        <label class="form-label">工作地點</label>
        <div class="accordion" id="accordionDisease">
          <div
            class="accordion-item"
            v-for="(item, index) in cityArea"
            :key="item.id"
          >
            <div class="accordion-header">
              <button
                class="accordion-button collapsed"
                type="button"
                data-bs-toggle="collapse"
                :data-bs-target="'#collapse' + (index + 100)"
              >
                {{ item.city_name }}
              </button>
            </div>
            <div
              :id="'collapse' + (index + 100)"
              class="accordion-collapse collapse"
              data-bs-parent="#accordionDisease"
            >
              <div class="accordion-body">
                <div class="input-list">
                  <div class="form-check-inline">
                    <input
                      type="checkbox"
                      class="form-check-input"
                      :value="item.id"
                      :id="item.id"
                      v-model="data.selectAllCityId"
                      @change="checkAll(item)"
                    />
                    <label class="form-check-label" :for="item.id">全區</label>
                  </div>
                  <br />
                  <div
                    class="form-check-inline"
                    v-for="item2 in item.areas"
                    :key="item2.id"
                  >
                    <input
                      type="checkbox"
                      class="form-check-input"
                      :value="item2.id"
                      :id="item2.id"
                      v-model="data.selectArea"
                      @change="cancelAll(item, item2, $event)"
                    />
                    <label class="form-check-label" :for="item2.id">{{
                      item2.area_name
                    }}</label>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="page1" v-show="showPage == 'page3'">
      <DiseaseType @disease-data="updateDiseaseData" />
    </div>
    <div class="page1" v-if="showPage == 'page4'">
      <div class="mb-3">
        <label class="form-label">照服員結業證書(照)</label>
        <input
          type="file"
          class="form-control"
          id="formFile1"
          @change="handleFileUpload(1, myUploadFile)"
          ref="myUploadFile"
          accept="image/png, image/jpeg"
          required
        />
      </div>
      <div class="mb-3">
        <label class="form-label">證號</label>
        <input
          type="text"
          class="form-control"
          placeholder="請輸入證號"
          v-model="data.license.certificateCode"
          required
        />
      </div>
      <div class="mb-3">
        <label class="form-label">一年內體檢表</label>
        <input
          type="file"
          class="form-control"
          id="formFile2"
          @change="handleFileUpload(2, myUploadFile2)"
          ref="myUploadFile2"
          accept="image/png, image/jpeg"
          required
        />
      </div>
      <div class="mb-3">
        <label class="form-label">良民證</label>
        <input
          type="file"
          class="form-control"
          id="formFile3"
          @change="handleFileUpload(3, myUploadFile3)"
          ref="myUploadFile3"
          accept="image/png, image/jpeg"
          required
        />
      </div>
      <div class="mb-3">
        <label class="form-label">其他證照</label>
        <input
          type="file"
          class="form-control"
          id="formFile4"
          @change="handleFileUpload(4, myUploadFile4)"
          ref="myUploadFile4"
          accept="image/png, image/jpeg"
          required
        />
      </div>
    </div>
  </div>
  <footer class="c-footer">
    <div class="ui-tabbtn">
      <div class="row">
        <div class="col">
          <button
            class="btn btn-primary"
            type="button"
            @click="save"
            v-if="showPage == 'page1'"
          >
            確認並繼續
          </button>
          <button
            class="btn btn-primary"
            type="button"
            @click="save2"
            v-if="showPage == 'page2'"
          >
            確認並繼續
          </button>
          <button
            class="btn btn-primary"
            type="button"
            @click="save3"
            v-if="showPage == 'page3'"
          >
            確認並繼續
          </button>
          <button
            class="btn btn-primary"
            type="button"
            @click="save4"
            v-if="showPage == 'page4'"
          >
            完成新增
          </button>
        </div>
      </div>
    </div>
  </footer>
</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";
@import "@/assets/custom/mixins";

Header {
  background-color: #ffffff;
}

.resumeAdd {
  margin-bottom: calc($footer-height + 15px);
  margin-top: $header-height;
  padding: 1.25rem;
}

.title {
  font-size: 1.25rem;
  font-weight: 700;
  display: flex;
  justify-content: center;
}
</style>
